import axios from '@/libs/api.request';
var userRole = new Object();

userRole.deleteUserRole = function (data, fnOk, fnError) {
  axios.request({
    url: "/userRole/delete?id=".concat(data.id),
    method: 'post'
  }).then(function (res) {
    console.log(res);

    if (res.data.code != "Success") {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};

userRole.addUserRole = function (data, fnOk, fnError) {
  axios.request({
    url: '/userRole/add',
    method: 'post',
    data: data
  }).then(function (res) {
    console.log(res);

    if (res.data.code != "Success") {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};

export default userRole;